.main {
   background: #070508;
   display: flex;
}

/* RESET */

*,
*::before,
*::after {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   overflow: hidden;
   color: #E3EFEF;

   --primary-color: #21E6ED;
   --secondary-color: #1B71A9;
   --tertiary-color: #0E1D25;
   --bg-color: #070508;
   --text-color: #F9FAFA;

   --gradient: linear-gradient(rgb(33, 230, 237), rgba(27, 113, 169,1), rgba(27, 113, 169,0.2) ,rgba(7, 5, 8, 0.5),rgba(7, 5, 8))
}
 
ul,
ol {
   list-style: none;
}

a {
   color: inherit;
   text-decoration: initial;
}

button {
   font: inherit;
   border: initial;
   outline: initial; /* create alternative for focus state */
   background-color: initial;
}

input {
   border: initial;
   outline: initial; /* create alternative for focus state */
   font: inherit;
}

/* scrollbar settings */

::-webkit-scrollbar {
   width: 6px;
}

::-webkit-scrollbar-track {
   background-color: transparent;
}

::-webkit-scrollbar-thumb {
   background-color: rgba(33, 230, 237, 0.5);
   border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
   background-color: var(--primary-color);
}